.app {
  min-height: 100vh;
  background-color: #fff;
}

.app-main {
  padding-bottom: 32px;
}
/* Loading animation (optional) */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.app-main > * {
  animation: fadeIn 0.3s ease-in-out;
}
/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
